.news-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  // margin-bottom: 20px;
  position: relative;
  margin-right: 30px;
  &__item {
    display: block;
    border-radius: 10px;
    // max-width: 33%;
    // flex-basis: calc(50% - 20px);
    flex-basis: calc(50% - 20px);
    &:nth-of-type(odd):not(:last-child) {
      margin-right: 40px;
    }
    &:nth-of-type(odd):last-child {
      // margin-right: 40px;
      flex-basis: 70%;
      margin: 0 auto;
      flex-grow: 0;
    }
    flex-grow: 1;
    // padding-bottom: 30vw;
    height: 30vw;
    max-height: 380px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    margin-bottom: 20px;
    position: relative;
    overflow: hidden;
    &:hover .news-grid__img {
      transform: scale(1.05);
    }
    // & .news-grid__title {
    //   font-size: 22px;
    //   line-height: 33px;
    //   font-weight: 700;
    // }
    &:hover .news-grid__title {
      text-decoration-color: $color-subtitle;
    }
    &::after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
    }
    &--blue::after {
      background: linear-gradient(180deg, #D8E0FF 27.67%, rgba(255, 255, 255, 0) 100%);
    }
    &--biege::after {
      background: linear-gradient(180deg, #F2EDED 27.67%, rgba(255, 255, 255, 0) 100%);
    }
    &--white::after {
      background: linear-gradient(180deg, #F4F6F3 27.67%, rgba(255, 255, 255, 0) 100%);
    }
    // @supports (flex: 0 0 calc(33.333% - 13px)) {
    //   flex: 0 0 calc(33.333% - 13px);
    // }
    // &--large {
    //   max-width: 66%;
    //   @supports (flex: 0 0 calc(67% - 10px)) {
    //     flex: 0 0 calc(67% - 10px);
    //   }
    // }
    // &--left {}
    // &--left-top {}
    // &--left-bottom {
    //   & .news-grid__content {
    //     top: auto;
    //     bottom: 25px;
    //   }
    // }
    // &--condensed {
    //   & .news-grid__title {margin-bottom: 15px;}
    //   & .news-grid__content {
    //     top: 30px;
    //     max-width: 220px;
    //   }
    // }
  }
  &__content {
    position: absolute;
    // top: 25px;
    // left: 25px;
    left: 50%;
    // top: 1.5em;
    top: 0;
    transform: translateX(-50%);
    z-index: 2;
    text-align: center;
    width: 100%;
    padding: 1.5em 10px;

    // padding-right: 10px;
  }
  &__title {
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
    color: $color-subtitle;
    margin-bottom: 5px;
    text-decoration: underline;
    text-decoration-color: transparent;
    transition: text-decoration-color .25s;


    // text-align: center;
    // max-width: 95%;

    font-size: 22px;
    line-height: 33px;
    font-weight: 700;
  }
  &__subtitle {
    font-size: 18px;
    line-height: 27px;
    color: #6C6A72;
    // text-align: center;
  }
  &__img {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    transition: .25s;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
}


