@media (max-width: 767px) {
  .news {
    padding-bottom: $pb-section-mobile;
  }
  .news-grid {
    &__content {
      // top: 20px;
      // left: 15px;
    }
    &__title {
      font-size: 16px;
      line-height: 24px;
      margin-bottom: 8px;
    }
